.container.nav_container {
  display: inline-block;
  width: 100%;
  max-width: 768px;
}

.header_container {
  padding-top: 46px;
  padding-bottom: 46px;
}

.nav_list {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.nav_item {
  padding-top: 10px;
}

.nav_item:not(:last-child) {
  margin-right: 40px;
}

.nav_list .home_link {
  position: relative;
  padding-bottom: 3px;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  color: white;
  text-decoration: none;
  transition: 0.4s;
}

.home_link.current::after {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background-color: #ff6b08;
  left: 0;
  bottom: 0;
  border-radius: 0px;
}

.nav_list .library_link {
  position: relative;
  font-size: 14px;
  padding-bottom: 3px;
  color: white;
  transition: 0.4s;
}

.library_link {
  border: none;
  color: white;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  cursor: pointer;
}

.library_link.current::after {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background-color: #ff6b08;
  left: 0;
  bottom: 0;
  border-radius: 0px;
  text-decoration: none;
}
